/**
 * @class BlocksAPI
 * provides with methods working with Block
 */
export default class BlocksAPI extends Module {
    /**
     * Save Editor config. API provides passed configuration to the Blocks
     * @param {EditorsConfig} config
     */
    constructor({ config }) {
        super({ config });
    }
    /**
     * Available methods
     * @return {IBlocksAPI}
     */
    get methods() {
        return {
            delete: () => this.delete(),
            moveDown: () => this.moveDown(),
            moveUp: () => this.moveUp(),
        };
    }
    /**
     * Moves block down
     */
    moveDown() {
        console.log('moving down', this.Editor.BlockManager);
    }
    /**
     * Moves block up
     */
    moveUp() {
        console.log('moving up', this.Editor.BlockManager);
    }
    /**
     * Deletes Block
     * @param blockIndex
     */
    delete(blockIndex) {
        this.Editor.BlockManager.removeBlock(blockIndex);
        this.Editor.Toolbar.close();
        this.Editor.BlockManager.navigatePrevious(true);
    }
}
